<template>
  <div>
    <div class="listTitle">
        <i></i>
        <span></span>
        <!-- <el-button
          class="fr"
          type="primary"
        @click="handleEdit(scope.$index, scope.row)"></el-button>-->
    </div>
    <el-table :data="tableData" border class="mb10" style="text-align:center" height="650" @selection-change="handleSelectionChange">
        <el-table-column type="index" label="" width="50"> </el-table-column>
          <el-table-column prop="patientName" label="" width="80"> </el-table-column>
          <el-table-column prop="medicalNo" label=""></el-table-column>
          <el-table-column prop="mobile" label=""> </el-table-column>
          <el-table-column prop="reservationDate" label="" width="160"> </el-table-column>
          <el-table-column prop="reservationDoctorName" label=""> </el-table-column>
          <el-table-column label="" prop="vistitingStatus">
            <template slot-scope="scope">
              <span v-if="scope.row.vistitingStatus === 'UNCONFIRMED'"><span class="indicatorA"><span></span></span></span>
              <span v-if="scope.row.vistitingStatus === 'TREATMENT'"><span class="indicatorB"><span></span></span></span>
              <span v-if="scope.row.vistitingStatus === 'CANCEL'"><span class="indicatorC"><span></span></span></span>
              <span v-if="scope.row.vistitingStatus === 'FINISHED'"><span class="indicatorD"><span></span></span></span>
            </template>
          </el-table-column>
          <el-table-column label="" width="160">
            <template slot-scope="scope">
              <span v-for="(item,index) in scope.row.serviceChooses" :key="item.code">
                {{item.projectPropertyName}}<span v-if="scope.row.serviceChooses.length -1 > index">/</span>
             </span>
            </template>
          </el-table-column>
          <el-table-column prop="createDate" label="" width="160"> </el-table-column>
          <el-table-column prop="createName" label=""> </el-table-column>
          <el-table-column prop="remark" label=""> </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <span v-if="scope.row.reservationType == 'RESERVATION' "></span>
              <span v-if="scope.row.reservationType == 'REGISTERED' "></span>
            </template>
          </el-table-column>
          <el-table-column prop="advisoryName" label=""> </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <span v-if="scope.row.patientType == 'PT' "></span>
              <span v-if="scope.row.patientType == 'LS' "></span>
            </template>
          </el-table-column>
          <el-table-column label="" width="240" fixed="right">
            <slot :row="scope.row" slot-scope="scope"></slot>
          </el-table-column>
      </el-table>
  </div>
</template>

<script>
export default {
  components:{},
  props:{
    tableData: {
      type: Array,
      default: []
    },
    tabConKeyName: {
      type: String,
      default: ''
    }
  },
  data(){
    return {
    }
  },
  watch:{},
  computed:{},
  methods:{
    handleSelectionChange() {
      this.$emit('handleSelectionChange')
    }
  },
  created(){},
  mounted(){}
}
</script>
<style lang="less" scoped>
</style>


